"use client";
import React, { useEffect } from "react";
import "./FeaturedVideoSection.css";
import { getContent } from "../../content/content";
import { useLanguage } from "../../context/LanguageContext";

const FeaturedVideoSection: React.FC = () => {
  const { lang } = useLanguage();
  const featuredVideoContent = getContent(lang).pages.landing.section.featuredVideoContent;
  
  // ✅ Ensure TikTok embed script loads after render
  useEffect(() => {
    const existingScript = document.querySelector("script[src='https://www.tiktok.com/embed.js']");
    if (!existingScript) {
      const script = document.createElement("script");
      script.src = "https://www.tiktok.com/embed.js";
      script.async = true;
      document.body.appendChild(script);
    }
  }, []);

  return (
    <section className="featured-video-section" id="featured-video">
      <div className="featured-video-container">
        <h2 className="featured-video-heading">{featuredVideoContent.heading}</h2>
        <h3 className="featured-video-subheading">
          {featuredVideoContent.subheading}
        </h3>

        {/* 🎬 YouTube Video (Desktop / Tablet) */}
        <div className="video-wrapper desktop-video">
          <iframe
            width="560"
            height="315"
            src="https://www.youtube.com/embed/MbXLt7OwEXI?si=n9HQ_JG8-N2aFsoR"
            title="Tinh Hoa Việt - Promotional Video"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            referrerPolicy="strict-origin-when-cross-origin"
            allowFullScreen
          ></iframe>
        </div>

        {/* 📱 TikTok Video (Mobile Only) */}
        <div className="video-wrapper mobile-video">
          <blockquote
            className="tiktok-embed"
            cite="https://www.tiktok.com/@vietnamtourismboard/video/7373988012513643818"
            data-video-id="7373988012513643818"
          >
            <section>
              <a
                target="_blank"
                href="https://www.tiktok.com/@vietnamtourismboard/video/7373988012513643818"
                rel="noreferrer"
              >
                Watch on TikTok
              </a>
            </section>
          </blockquote>
        </div>
      </div>
    </section>
  );
};

export default FeaturedVideoSection;
