// NominationCTA.tsx
import React from "react";
import "./NominationCTA.css";
import { FiArrowRight } from "react-icons/fi";
import { Link } from "gatsby";


import { getContent } from "../../content/content";
import { useLanguage } from "../../context/LanguageContext";


const NominationCTA: React.FC = () => {
  const { lang } = useLanguage();
  const nominationCtaContent = getContent(lang).pages.landing.section.nominationCtaContent;
  
  const { heading, description, cta } = nominationCtaContent;

  return (
    <section className="cta-section">
      <div className="footer-grid-bg"></div>
      <div className="cta-container">
        <h2 className="cta-heading">{heading}</h2>
        <p className="cta-text">{description}</p>

        <div className="thv-hero__cta-row cta-buttons">
          <Link to={cta.href} className="thv-btn thv-btn--gold">
            {cta.label}
            <FiArrowRight aria-hidden="true" />
          </Link>
        </div>
      </div>
    </section>
  );
};

export default NominationCTA;
