// About.tsx
import React from "react";
import "./about.css";


import { useLanguage } from "../../context/LanguageContext";
import { getContent } from "../../content/content_fixed";

const About: React.FC = () => {
  const { lang } = useLanguage();
  const aboutContent = getContent(lang).pages.landing.section.aboutContent;

  return (
    <section className="intro-section" id={aboutContent.sectionId}>
      <div className="intro-container">
        {/* Left Text */}
        <div className="intro-text">
          <h2 className="intro-heading">{aboutContent.heading}</h2>
          <h3 className="intro-subline">{aboutContent.subline}</h3>
          
          {aboutContent.paragraphs.map((para : any , index : number ) => (
            <p className="intro-body" key={index}>{para}</p>
          ))}

          <a href={aboutContent.link.href} className="intro-link">
            {aboutContent.link.label}
          </a>
        </div>

        {/* Right Image */}
        <div className="intro-image">
          <img
            src={aboutContent.image.src}
            alt={aboutContent.image.alt}
          />
        </div>
      </div>
    </section>
  );
};

export default About;
